import { Component, ViewChild, ElementRef, NgZone } from '@angular/core';
import { IonicPage, NavController, NavParams, ModalController, ActionSheetController } from 'ionic-angular';
import { FormBuilder, FormGroup, FormArray, Validators } from '@angular/forms';
import { AdminPrivilegeProvider } from '../../providers/admin-privilege/admin-privilege';
import { ToastProvider } from '../../providers/toast';
import { LoaderProvider } from '../../providers/loader';
import { EventsService } from '../../providers/event.service';
import { Trucks } from '../../providers/truck';
import { MapsAPILoader } from '@agm/core';
import { HomePage } from '../home/home';
import { CommonHelper } from '../../helpers/common';

/**
 * Generated class for the EditCompanyDetailsPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-edit-company-details',
  templateUrl: 'edit-company-details.html',
})
export class EditCompanyDetailsPage {


  
  companyFlag = true;
  // bankFlag = false;
  addressFlag = false;
  locationFlag = false;
  // routeFlag = false;
  requirementFlag = false;

  list;
  editForm: FormGroup;
  address: FormArray;
  idx;
  requirementFormData;
  listData;
  
  obj = {
    owner_name: '',
    omobile_no_1: '',
    omobile_no_2: '',
    website: '',

    contact_person: '',
    gstin_no: '',
    location: '',
    mobile_no_1: '',
    mobile_no_2: '',
    company_name: '',
    company_requirement: [],
    address: [],
    materials: [],
    truck_types: [],
    capacity: [],
    // location_allotment: [],
    location_restriction: [],
    latitude: [],
    longitude: []
  }
  formValues;
  submitted = false;

  materialsList = [];
  truckTypesList = [];
  truckCapacityList = [];

  materials = [];
  truckTypes = [];
  truckCapacity = [];
  
  mainMaterials = [];
  mainTruckType = [];
  mainCapacity = [];

  @ViewChild("search2")
  public searchElementRefCity: ElementRef;

  @ViewChild("search3")
  public searchElementRefState: ElementRef;
  
  @ViewChild("search")
  public searchElementRef: ElementRef;

  constructor(
    public navCtrl: NavController, 
    public navParams: NavParams,
    public fb: FormBuilder,
    public admin: AdminPrivilegeProvider,
    public actionSheetController: ActionSheetController,
    public modalCtrl: ModalController,
    public loader: LoaderProvider,
    public toast: ToastProvider,
    public events: EventsService,
    public trucks: Trucks,
    private mapsAPILoader: MapsAPILoader,
    private ngZone: NgZone
    ) {
    this.list = this.navParams.get('data');
  
        
    this.getCapacity();
    this.getMaterials();
    
    let getLat = this.list.latitude;
    let getLng = this.list.longitude;
    localStorage.setItem('latlng', JSON.stringify({
      lat: getLat,
      lng: getLng
    }));


    this.editForm = this.fb.group({

      owner_name: ['', Validators.compose([Validators.required, Validators.minLength(2), Validators.maxLength(25)])],
      omobile_no_1: ['', Validators.compose([Validators.required, Validators.minLength(10), Validators.maxLength(10)])],
      omobile_no_2: ['', Validators.compose([Validators.minLength(10), Validators.maxLength(10)])],
      website: [''],

      company_name: ['', Validators.compose([Validators.required, Validators.minLength(2), Validators.maxLength(25)])],
      location: ['', Validators.compose([Validators.required, Validators.minLength(2), Validators.maxLength(25)])],
      gstin_no: ['', Validators.compose([Validators.minLength(15), Validators.maxLength(15)])],
      contact_person: ['', Validators.compose([Validators.required, Validators.minLength(2), Validators.maxLength(25)])],
      mobile_no_1: ['', Validators.compose([Validators.required, Validators.minLength(10), Validators.maxLength(10)])],
      mobile_no_2: ['', Validators.compose([Validators.minLength(10), Validators.maxLength(10)])],
      company_requirement: [''],
      address: this.fb.array([ this.createAddressObj(this.list.address[0])]),
      materials: [''],
      destinations: [''],
      truck_types: [''],
      capacity: [''],
      // location_allotment: [''],
      location_restriction: [''],
      latitude: [''],
      longitude: [''],
    });

    if(this.list) {
      this.editForm.controls['owner_name'].setValue(this.list.owner_name);
      this.editForm.controls['omobile_no_1'].setValue(this.list.omobile_no_1);
      this.editForm.controls['omobile_no_2'].setValue(this.list.omobile_no_2);
      this.editForm.controls['website'].setValue(this.list.website);

      this.editForm.controls['company_name'].setValue(this.list.company_name);
      this.editForm.controls['location'].setValue(this.list.location);
      this.editForm.controls['gstin_no'].setValue(this.list.gstin_no);
      this.editForm.controls['contact_person'].setValue(this.list.contact_person);
      this.editForm.controls['mobile_no_1'].setValue(this.list.mobile_no_1);
      this.editForm.controls['mobile_no_2'].setValue(this.list.mobile_no_2);

      // this.editForm.controls['location_allotment'].setValue(this.list.location_allotment);
      this.editForm.controls['location_restriction'].setValue(this.list.location_restriction);
      this.editForm.controls['latitude'].setValue(this.list.latitude);
      this.editForm.controls['longitude'].setValue(this.list.longitude);

      this.list.company_requirement.forEach(element => {
        this.obj.company_requirement.push(element);
      });
      
    }
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad EditCompanyDetailsPage');
  }
  
  Home(){
    this.navCtrl.setRoot(HomePage);
  }
  
  getLocation(e){
    e.preventDefault();
    this.events.broadcast('location');
  }

  createAddressObj(add): FormGroup  {
    return this.fb.group({
      address_line_1: [add.address_line_1, Validators.compose([Validators.required, , Validators.minLength(2)])],
      address_line_2: [add.address_line_2, Validators.compose([Validators.required, , Validators.minLength(2)])],
      city: [add.city, Validators.compose([Validators.required, , Validators.minLength(2)])],
      state: [add.state, Validators.compose([Validators.required, , Validators.minLength(2)])],
      pincode: [add.pincode, Validators.compose([Validators.required, Validators.minLength(6), Validators.maxLength(6)])]
    })
  }

  // Get controls for address form array
  get addressForm() {
    return this.editForm.get('address')['controls'][0].controls;
  }

  // Google map address
  addressData(e: any):void {
    this.addressForm['address_line_1'].setValue(e.address_line1);
    this.addressForm['address_line_2'].setValue(e.address_line2);
    this.addressForm['city'].setValue(e.city);
    this.addressForm['state'].setValue(e.state);
    this.addressForm['pincode'].setValue(e.pincode);

    this.obj.address = [];
    let obj = {
      address_line_1: e.address_line1,
      address_line_2: e.address_line2,
      city: e.city,
      state: e.state,
      pincode: e.pincode,
      address_location: {
        type : "Point",
        coordinates: []
      }
    }

    this.obj.address.push(obj);

    this.obj.address[0].address_location.coordinates = [];
    this.obj.address[0].address_location.coordinates.push(e.lng);
    this.obj.address[0].address_location.coordinates.push(e.lat);
  }

  update(isValid, value) {
    this.submitted = true;
    if(isValid) {

      this.obj.owner_name = value.owner_name;
      this.obj.omobile_no_1 = value.omobile_no_1;
      this.obj.omobile_no_2 = value.omobile_no_2;
      this.obj.website = value.website;

      this.obj.contact_person = value.contact_person;
      this.obj.gstin_no = value.gstin_no;  
      this.obj.location = value.location;
      this.obj.mobile_no_1 = value.mobile_no_1;
      this.obj.mobile_no_2 = value.mobile_no_2;
      this.obj.company_name = value.company_name;
      this.obj.address = value.address;
      this.obj.materials = this.mainMaterials;
      this.obj.truck_types = this.mainTruckType;
      this.obj.capacity = this.mainCapacity;

      // this.obj.location_allotment = value.location_allotment;
      this.obj.location_restriction = value.location_restriction;
      this.obj.latitude = value.latitude;
      this.obj.longitude = value.longitude;
      console.log(">>>FormVal2", this.obj);

      this.loader.show();
      this.admin.updateCompanyList(this.list._id, this.obj).subscribe((res: any) => {
        this.loader.hide();
        this.toast.show({ 'message': 'Updated successfully.' });
        this.navCtrl.setRoot('ManageCompanyListPage');
        console.log(">>>>>res", res);
      }, err => {
        this.loader.hide();
        this.toast.error({ 'message': 'Something went wrong.' });
        console.log(">>>>>err", err);
      });

    }
  }

  async presentActionSheet(companyData, idx) {
    const actionSheet = await this.actionSheetController.create({
      title: 'Actions',
      buttons: [{
        text: 'Edit company requirements',
        icon: 'create',
        handler: () => {
          this.openRequirementModal(companyData, idx);
        }
      }, {
        text: 'Delete this requirements',
        icon: 'trash',
        handler: () => {
          this.deleteRequirement(idx);
        }
      }]
    });
    await actionSheet.present();
  }

  deleteRequirement(idx){
    let conf = confirm('Are you sure you want to delete this requirements ?')
    if(conf) {
      this.loader.show();
      this.obj.company_requirement.splice(idx, 1);
      this.toast.show({ 'message': 'Deleted successfully.' });
      this.loader.hide();
    }
  }

  // add requirements modal
  addRequirementModal(e) {
    e.preventDefault();
    const modal = this.modalCtrl.create('EditCompanyRequirementsPage', {type: 'add'});
    modal.onDidDismiss(data => {
      console.log(data);
      if(data) {
        this.obj.company_requirement.push(data.requirementFormData);
      }
    });
    modal.present();
  }

  openRequirementModal(companyData, idx) {
    const modal = this.modalCtrl.create('EditCompanyRequirementsPage', {data: companyData, index: idx, companyListData: this.list, type: 'edit'});
    modal.onDidDismiss(data => {
      console.log(data);
      if(data) {
        this.listData = data.listData;
        this.requirementFormData = data.requirementFormData;
        this.idx = data.index;
      
        this.obj.company_requirement[idx].material = this.requirementFormData.material;
        this.obj.company_requirement[idx].to_destination = this.requirementFormData.to_destination;
        this.obj.company_requirement[idx].vehicle_capacity = this.requirementFormData.vehicle_capacity;
        this.obj.company_requirement[idx].vehicle_type = this.requirementFormData.vehicle_type;
      }
    });
    modal.present();
  }



  getCapacity(){
    this.trucks.getCapacityOfTruck().subscribe((res: any) => {
      var data = res.json(); 
      data.results.forEach(r => {
        r.capacity.forEach(r => {
          this.truckCapacity.push(r.ton);
        });
        this.truckTypes.push(r.type);
      });
      
      var newTruckCapacity=  Array.from(new Set(this.truckCapacity)); 
      console.log('truckTypes >>>>>>> ', this.truckTypes);
      console.log('Capacity >>>>>>> ', newTruckCapacity.sort((a,b) => a - b));
      this.truckCapacity = [];
      this.truckCapacity =  newTruckCapacity.sort((a,b) => a - b);


      this.truckCapacity.forEach(element => {
        var flag = false;
        this.list.capacity.forEach(r => {
          if(element == r) {
            flag = true;
            this.mainCapacity.push(element);
          }
        });
        var obj = {"name": element, "flag": flag};
        this.truckCapacityList.push(obj);
      });

      console.log('mainCapacity >>>>>>> ', this.mainCapacity);
      console.log('truckCapacityList >>>>>>> ', this.truckCapacityList);

      this.truckTypes.forEach(element => {
        var flag = false;
        this.list.truck_types.forEach(r => {
          if(element == r) {
            flag = true;
            this.mainTruckType.push(element);
          }
        });
        var obj = {"name": element, "flag": flag};
        this.truckTypesList.push(obj);
      });

      console.log('mainTruckType >>>>>>> ', this.mainTruckType);
      console.log('truckTypesList >>>>>>> ', this.truckTypesList);

    }, err => {
      console.log(">>>error", err);
    });
  }

  
  getMaterials(){
    this.trucks.getMaterials().subscribe((res: any) => {
      var data = res.json(); 
      data.results.forEach(r => {
        this.materials.push(r.name);
      });
      console.log('Materials data >>>>>>> ', this.materials);

      this.materials.forEach(element => {
        var flag = false;
        this.list.materials.forEach(r => {
          if(element == r) {
            flag = true;
            this.mainMaterials.push(element);
          }
        });
        var obj = {"name": element, "flag": flag};
        this.materialsList.push(obj);
      });

      console.log("this.list >>", this.materialsList);

    }, err => {
      console.log(">>>error", err);
    });
  }

  
  addMaterials(e, m, ind) {
    if ( e.target.checked ) {
      this.mainMaterials.push(m);
      this.materialsList[ind].flag = true;
    } else {
      var i = 0;
      this.mainMaterials.forEach(element => {
        if (element == m) {
          console.log(" IN    >>>>", i);
          this.mainMaterials.splice(i, 1);
        }
        i = i + 1;
      });
      console.log("this.mainMaterials >>>>", this.mainMaterials);
    }
  }

  
  addTruckType(e, tt, ind) {
    
    if ( e.target.checked ) {
      this.mainTruckType.push(tt);
      this.truckTypesList[ind].flag = true;
    } else {
      var i = 0;
      this.mainTruckType.forEach(element => {
        if (element == tt) {
          console.log(" IN    >>>>", i);
          this.mainTruckType.splice(i, 1);
        }
        i = i + 1;
      });
      console.log("this.mainTruckType >>>>", this.mainTruckType);
    }

  }

  
  addCapacity(e, c, ind) {

    if ( e.target.checked ) {
      this.mainCapacity.push(c);
      this.truckCapacityList[ind].flag = true;
    } else {
      var i = 0;
      this.mainCapacity.forEach(element => {
        if (element == c) {
          console.log(" IN    >>>>", i);
          this.mainCapacity.splice(i, 1);
        }
        i = i + 1;
      });
      console.log("this.mainCapacity >>>>", this.mainCapacity);
    }

  }

  preCompany() {
    this.companyFlag = true;
    this.addressFlag = false;
    this.locationFlag = false;
    this.requirementFlag = false;
  }

  nextAddress() {
    this.companyFlag = false;
    this.addressFlag = true;
    this.locationFlag = false;
    this.requirementFlag = false;

    setTimeout(() => {
      this.mapsAPILoader.load().then(() => {
        let presentCity = new google.maps.places.Autocomplete(this.searchElementRefCity.nativeElement, {
          componentRestrictions: {"country": "IN"},
          types: []
        });
        presentCity.addListener("place_changed", () => {
          this.ngZone.run(() => {
            //get the place result
            let place: google.maps.places.PlaceResult = presentCity.getPlace();
            let components = CommonHelper.extract_components(place);
            let pre_city = CommonHelper.get_sublocality(components);
            console.log(components, pre_city);
            
            (<HTMLInputElement>document.getElementById('City')).value = pre_city;

            let addr = this.editForm.get('address') as FormArray;
            let first = addr.at(0);
            first.get('city').setValue(pre_city);
          });
        });

        let presentState = new google.maps.places.Autocomplete(this.searchElementRefState.nativeElement, {
          componentRestrictions: {"country": "IN"},
          types: []
        });
        presentState.addListener("place_changed", () => {
          this.ngZone.run(() => {
            //get the place result
            let place: google.maps.places.PlaceResult = presentState.getPlace();
            let components = CommonHelper.extract_components(place);

            (<HTMLInputElement>document.getElementById('State')).value = components.administrative_area_level_1;

            let addr = this.editForm.get('address') as FormArray;
            let first = addr.at(0);
            first.get('state').setValue(components.administrative_area_level_1);

          });
        }); 
      });
    }, 2000);
  }

  nextLocation() {
    this.companyFlag = false;
    this.addressFlag = false;
    this.locationFlag = true;
    this.requirementFlag = false;

    
    setTimeout(() => {
      this.mapsAPILoader.load().then(() => {
        
        // set default location
        // localStorage.setItem('latlng', JSON.stringify({
        //   lat: getLat,
        //   lng: getLng
        // }));
        // this.events.broadcast('location');
  
        let presentCity = new google.maps.places.Autocomplete(this.searchElementRef.nativeElement, {
          componentRestrictions: {"country": "IN"},
          types: []
        });
        presentCity.addListener("place_changed", () => {
          this.ngZone.run(() => {
            //get the place result
            let place: google.maps.places.PlaceResult = presentCity.getPlace();
            let lat = place.geometry.location.lat();
            let lng = place.geometry.location.lng();
            localStorage.setItem('latlng', JSON.stringify({
              lat: lat,
              lng: lng
            }));
            this.events.broadcast('location');
            this.editForm.controls['latitude'].setValue(lat);
            this.editForm.controls['longitude'].setValue(lng);

            let components = CommonHelper.extract_components(place);
            let pre_city = CommonHelper.get_sublocality(components);
            console.log(components, pre_city);

            (<HTMLInputElement>document.getElementById('Location')).value = pre_city;
            this.editForm.controls['location'].setValue(pre_city);

          });
        });
    
      });
    }, 4000);
  }

  nextReq() {
    this.companyFlag = false;
    this.addressFlag = false;
    this.locationFlag = false;
    this.requirementFlag = true;
  }

}



